.lk {
  display: none !important;
}

.dashboard {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  height: 100%;

  .left {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 20%;
    background: #8c798f;
    padding: 3%;
    height: 100%;

    .top {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    h2 {
      color: #fff;
      font-family: "Rubik";
      font-size: 30px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-transform: uppercase;
    }

    p {
      color: #fff;
      text-align: center;
      font-family: "Rubik";
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-transform: uppercase;
    }

    .menu {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      margin-top: 5%;

      p {
        color: rgba(255, 255, 255, 0.6);
        font-family: "Rubik";
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-transform: uppercase;
      }

      button {
        border: none;
        background: none;
        width: 100%;
        margin: 0%;
        padding: 0%;
      }
    }

    .bottom {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      margin-top: 20%;

      a {
        width: 100%;
      }

      p {
        color: rgba(255, 255, 255, 0.6);
        font-family: "Rubik";
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-transform: uppercase;
      }
    }
  }

  .right {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    width: 67%;
    padding: 1%;
    margin-right: 3%;

    .top {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      border-radius: 10px;
      background: #282828;
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
      padding: 1% 2%;

      .text {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        h3 {
          color: #fff;
          text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
          font-family: "Rubik";
          font-size: 30px;
          font-style: normal;
          font-weight: 500;
          line-height: normal;
          margin: 0%;
        }

        p {
          color: rgba(255, 255, 255, 0.5);
          font-family: "Rubik";
          font-size: 24px;
          font-style: normal;
          font-weight: 300;
          line-height: normal;
          margin: 0%;
        }
      }

      .user {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 30%;

        p {
          color: #fff;
          font-family: "Rubik";
          font-size: 24px;
          font-style: normal;
          font-weight: 500;
          line-height: normal;
          margin-left: 10%;
        }
      }
    }

    .main {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: space-between;
      width: 100%;
      margin-top: 2%;

      .info {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 35%;
        border-radius: 20px;
        background: #282828;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        padding: 3%;

        .discord {
          display: none;
        }

        img {
          width: 100%;
        }

        .data {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          width: 100%;

          h4 {
            color: #fff;
            font-family: "Rubik";
            font-size: 30px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            margin: 3% 0;
          }

          form {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;
            width: 100%;
            .name {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              width: 100%;

              input {
                width: 40%;
              }
            }

            input {
              border: none;
              border-bottom: 1px solid #fff;
              background: none;
              padding: 1% 0;
              margin-top: 7%;
              width: 100%;
              color: #fff;
              font-family: "Rubik";
              font-size: 24px;
              font-style: normal;
              font-weight: 300;
              line-height: normal;
            }

            input::-webkit-input-placeholder {
              color: #fff;
              font-family: "Rubik";
              font-size: 24px;
              font-style: normal;
              font-weight: 300;
              line-height: normal;
            }

            button {
              border-radius: 10px;
              background: rgba(214, 185, 252, 0.7);
              color: #fff;
              font-family: "Rubik";
              font-size: 24px;
              font-style: normal;
              font-weight: 500;
              line-height: normal;
              text-transform: uppercase;
              width: 100%;
              padding: 3% 0;
              margin-top: 10%;
            }
          }
        }
      }

      .second {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 55%;
        margin-left: 5%;

        .discord {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          width: 100%;
          border-radius: 20px;
          background: #282828;
          box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
          padding: 3% 5%;

          h3 {
            color: #fff;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-family: "Rubik";
            font-size: 30px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            margin: 2% 0;
          }

          img {
            width: 100%;
          }

          .account {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            margin: 5% 0;

            img {
              width: 10%;
            }

            .username {
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              justify-content: space-between;
              width: 100%;
              margin-left: 2%;

              p {
                color: #fff;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                font-family: "Rubik";
                font-size: 24px;
                font-style: normal;
                font-weight: 300;
                line-height: normal;
                margin: 0%;
              }

              span {
                color: #27a342;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                font-family: "Rubik";
                font-size: 24px;
                font-style: normal;
                font-weight: 300;
                line-height: normal;
              }
            }
          }
        }

        .events {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          width: 100%;
          border-radius: 20px;
          background: #282828;
          box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
          padding: 3% 5%;
          margin-top: 3%;
          h3 {
            color: #fff;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-family: "Rubik";
            font-size: 30px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            margin: 2% 0;
          }

          img {
            width: 100%;
          }

          p {
            color: #fff;
            font-family: "Rubik";
            font-size: 20px;
            font-style: normal;
            font-weight: 300;
            line-height: normal;
          }
        }

        .about {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          width: 100%;
          border-radius: 20px;
          background: #282828;
          box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
          padding: 1% 5%;
          margin-top: 3%;

          h3 {
            color: #fff;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-family: "Rubik";
            font-size: 30px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            margin: 2% 0;
          }

          img {
            width: 100%;
          }

          .block {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            width: 100%;

            h5 {
              color: #fff;
              font-family: "Rubik";
              font-size: 20px;
              font-style: normal;
              font-weight: 500;
              line-height: normal;
              margin: 2% 0;
            }

            p {
              color: #fff;
              font-family: "Rubik";
              font-size: 20px;
              font-style: normal;
              font-weight: 300;
              line-height: normal;
              margin: 1%;
            }

            span {
              color: #ffaa50;
              font-family: "Rubik";
              font-size: 24px;
              font-style: normal;
              font-weight: 300;
              line-height: normal;
              margin: 2% 1%;
            }
          }
        }
      }
    }
    .calendar {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      width: 70%;
      border-radius: 10px;
      background: #282828;
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
      margin-top: 3%;
      padding: 3%;

      h3 {
        color: #fff;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        font-family: "Rubik";
        font-size: 30px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
      }

      .wrapper {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;

        .cal {
          padding-bottom: 8%;
        }
      }

      .booking {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        width: 50%;
        // margin-left: 1%;

        h4 {
          color: #fff;
          font-family: "Rubik";
          font-size: 24px;
          font-style: normal;
          font-weight: 500;
          line-height: 21px; /* 87.5% */
          margin: 0%;
        }

        span {
          color: rgba(255, 255, 255, 0.5);
          font-family: "Rubik";
          font-size: 18px;
          font-style: normal;
          font-weight: 300;
          line-height: 21px; /* 116.667% */
          margin-top: 5%;
        }

        p {
          color: #fff;
          font-family: "Rubik";
          font-size: 20px;
          font-style: normal;
          font-weight: 300;
          line-height: 21px; /* 105% */
          // margin: 0%;
        }

        button {
          border-radius: 10px;
          border: 2px solid #d6b9fc;
          background: none;
          color: #fff;
          text-align: center;
          font-family: "Rubik";
          font-size: 20px;
          font-style: normal;
          font-weight: 500;
          line-height: 21px; /* 105% */
          padding: 3% 5%;
          width: 100%;
          margin-top: 5%;
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .dashboard{
    .left {
      min-height:100% ;
      .bottom {
        margin-top: 240% !important;
        // align-self: flex-end;
      }
    }
  }

  .right {
    align-items: center !important;
    .main {
      flex-direction: column !important;
      align-items: center !important;
      width: 100% !important;

      .info {
        width: 100% !important;
        
        .element {
          display: flex;
          flex-direction: row;
          align-items: flex-start;
        }

        .discord {
          display: flex !important;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          width: 100%;
          border-radius: 20px;
          padding: 0 5%;

          .change {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 90%;

            .bin {
              width: 50px;
              height: 50px;
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: center;
              border-radius: 10px;
                background: rgba(217, 217, 217, 0.10);
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                -ms-border-radius: 10px;
                -o-border-radius: 10px;
              img {
                width: 80% !important;
              }
              
            }


            button {
              border-radius: 10px;
              background: rgba(217, 217, 217, 0.10);
              color: #FFF;
              font-family: "Rubik";
              font-size: 20px;
              font-style: normal;
              font-weight: 300;
              line-height: normal;
              border: none;
              padding: 2% 3%;
            }
          }

          h3 {
            color: #fff;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-family: "Rubik";
            font-size: 20px !important;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            margin: 2% 0;
          }

          img {
            width: 100%;
          }

          .account {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            margin: 5% 0;

            img {
              width: 20% !important;
            }

            .username {
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              justify-content: space-between;
              width: 100%;
              margin-left: 2%;

              p {
                color: #fff;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                font-family: "Rubik";
                font-size: 20px !important;
                font-style: normal;
                font-weight: 300;
                line-height: normal;
                margin: 0%;
              }

              span {
                color: #27a342;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                font-family: "Rubik";
                font-size: 20px !important;
                font-style: normal;
                font-weight: 300;
                line-height: normal;
              }
            }
          }
        }

        img {
          width: 40% !important;
        }
      }

      form {
        input {
          margin-top: 3% !important;
        }
      }

      .discord {
        display: none !important;
      }

      .second {
        width: 100% !important;

        img {
          // width: 100% !important;
        }

        .account{
          img {
            width: 10% !important;
          }
        }
      }
    }

    .top {
      padding: 2% 2%  !important;
    }

    .text {
      p {
        font-size: 20px !important;
      }
    }

    .user {
      display: none !important;
    }

    .events {
      padding: 3% !important;
      width: 100%;
      margin-left: -5%;

      h3 {
        font-size: 20px;
      }

      p {
        font-size: 15px;
      }
    }

    .about {
      padding: 3% !important;
      width: 100%;
      margin-left: -5%;

      h3 {
        font-size: 20px !important;
      }

      .block {
        p, span {
          font-size: 20px !important;
        }
      }
    }
  }
}

@media(max-width:768px) {
  .lk {
    display: block !important;
  }

  .dashboard {
    flex-direction: column;
    align-items: center;

    .menu {
      display: block;
    }

    .left {
      display: none !important;
    }

    .right {
      width: 90%;
      margin: auto;

      .text {
        h3 {
          font-size: 30px;
        }

        p {
          font-size: 15px !important;
        }
      }

      .main {
        .info {
          .element {
            .discord {
              h3 {
                font-size: 15px !important;
              }

              .account {
                .username {
                  p {
                    font-size: 13px !important;
                  }

                  span {
                    font-size: 13px !important;
                  }
                }
              }

              .change {
                flex-direction: row-reverse;
                width: 100%;

                button {
                  font-size: 15px !important;
                  margin-right: 5%;
                }
              }
            }
          }

          .data {
            h4 {
              font-size: 15px;
            }

            input {
              font-size: 15px;
            }
          }
        }
      }
    }
  }
  
}

