        .discord {
          h3 {
            font-size: 15px !important;
          }

          .account {
            .username {
              p {
                font-size: 13px !important;
              }

              span {
                font-size: 13px !important;
              }
            }
          }

          .change {
            flex-direction: row-reverse;
            width: 100%;

            button {
              font-size: 15px !important;
              margin-right: 5%;
            }
          }
        }
      }

      .data {
        h4 {
          font-size: 15px !important;
        }

        form {
          button {
            font-size: 15px !important;
          }
        }

        .name {
          width: 100% !important;
          flex-direction: column !important;

          input {
            width: 100% !important;
          }

        }

        input {
          font-size: 15px !important;
        }

        input::-webkit-input-placeholder {
          font-size: 15px !important;
        }
      }
    }

    .second {
      .events {
        h3 {
          font-size: 15px !important;
        }

        p {
          font-size: 13px !important;
        }
      }

      .about {
        h3 {
          font-size: 15px !important;
        }

        p, span {
          font-size: 13px !important;
        }

        h5 {
          font-size: 13px !important;
        }
      }
    }
  }

}